.cart {
  @include container--content();


  &__tab-content {
    opacity: 0;
    max-height: 0;
    overflow: hidden;
    padding: 0;
    pointer-events: none;
    &--active {
      opacity: 1;
      max-height: unset;
      padding: unset;
      pointer-events: all;
      transition: opacity $transition; } }


  &__wrapper {
    display: flex;
    margin-top: 21px;
    &--tab-2 {
      margin-top: 54px; } }


  &__items {
    margin-top: 29px; }

  &__item {
    position: relative;
    display: flex; }

  &__item + &__item {
    margin-top: 37px;
    &:before {
      content: "";
      position: absolute;
      right: 6px;
      top: -22px;
      width: calc(100% - 140px);
      height: 1px;
      @include gbackground(100%); } }


  &__item-img-wrap {
    margin: 0 0 0 11px;
    max-width: 106px; }

  &__item-img {
    width: 100%; }

  &__item-text {
    margin: 8px 23px 0 17px;
    display: flex;
    max-width: 198px;
    flex-flow: column nowrap; }

  &__item-name {
    @include subtitle(1.8rem);
    font-weight: 900;
    line-height: 100%; }

  &__item-price {
    margin-top: 2px;
    @include subtitle(2.4rem);
    font-weight: 400;
    text-transform: none; }

  &__item-code {
    margin-top: 10px;
    @include subtitle(1.4rem); }


  &__item-ui {
    width: 180px;
    padding-top: 10px; }

  &__item-ui-text {
    display: block;
    @include subtitle(1.8rem);
    font-weight: 900;
    text-align: center; }

  &__item-ui-wrap {
    display: flex;
    margin-top: 17px; }

  &__item-number {
    @include resetinteractive();
    display: block;
    flex-shrink: 1;
    max-width: 38%;
    flex-basis: 38%;
    @include subtitle(2.4rem);
    font-weight: 900;
    text-align: center;
    background-color: $white;
    &:focus, &:active {
      outline: 1px solid darken($white, 20%); } }


  &__item-btn {
    @include resetinteractive();
    padding: 12px 5px 12px 22px;
    flex-basis: 25%;
    background-color: $white;
    cursor: pointer;
    z-index: 3;
    transition: background-color $transition/3;
    &:active {
      outline: 0;
      background-color: darken($white, 20%); }
    &:focus {
      outline: 1px solid darken($white, 10%); }
    &:hover {
      svg {
        transform: rotate(180deg) translateX(2px); } }
    svg {
      transform: rotate(180deg);
      transition: transform $transition; }
    &--add {
      padding: 12px 22px 12px 5px;
      svg {
        transform: rotate(0); }
      &:hover {
        svg {
          transform: rotate(0deg) translateX(2px); } } } }

  &__item-close {
    @include resetinteractive();
    margin: 5px 0 0 68px;
    align-self: flex-start;
    svg {
      transform: translateY(0);
      transition: transform $transition/3; }
    &:focus {
      outline: 0;
      svg {
        stroke: url(#linear-grad-2); } }
    &:active {
      svg {
        transform: translateY(-2px); } } }



  &__total-price {
    position: relative;
    margin-left: auto;
    width: 400px;
    height: 227px;
    padding: 39px 59px 45px 69px; }

  &__price-border {
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none; }

  &__total-price-title {
    display: block;
    @include subtitle(2.4rem);
    font-weight: 900; }

  &__total-price-value {
    display: block;
    margin-top: 3px;
    @include subtitle(2.4rem);
    text-transform: none; }

  &__total-price-proceed-btn {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-top: 43px;
    padding: 7px 17px 7px 32px;
    &:hover {
      svg {
        transform: translateX(7px); } }

    span {
      letter-spacing: 0.6px; }
    svg {
      position: relative; } }

  &__delivery-info {
    margin-top: 110px;
    max-width: 665px; }

  &__delivery-title {
    @include subtitle(3.6rem);
    font-weight: 900; }

  &__delivery-text {
    p {
      font-size: 1.8rem;
      line-height: 3rem; }
    p + p {
      margin-top: 38px; } } }


